.color_Word {
    display: inline-block;
    font-size: 15px;
    padding-right: 5px;
}
.color_Word__Hilighted{
    color: red;
}

.ph-mem{
    padding: 5px;
}

.ph-mem__toolbar{
    margin-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
    row-gap: 10px; 
}

.toolbar-button {
    width: 64px;
    height: 64px;
    background-color: blanchedalmond;
    border-radius: 10px;
    margin-right: 10px;
}

.toolbar-button_pressed{
    background-color: lightgreen;
}

.toolbar-button__enabled {
    background-color: #d3ffd3;
}

.toolbar-button__disabled {
    background-color: #f5f5f5;
    color: lightgray;
    border-color: lightgray;
}



.ph-mem__header{
    font-size: 40px;
    font-weight: 700;
    color: darkcyan;
    border-bottom: solid 3px;
    padding-left: 10px;
}

.settings-bool-item{
    color: lightslategray;
    font-size: 20px;
    font-weight: 600;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    max-width: 600px;
}

.settings_ddown{
    position: relative;
    top: 3px
}


